import { CommonService } from '@/services/api/common.service';
import { PropertyService } from '@/services/api/property.service';
import { Component, Listen, Prop, State, Watch, h } from '@stencil/core';
import { Locale } from 'date-fns';
import { ICurrency, IExposedLanguages, pages } from '@/models/common';
import axios from 'axios';
import { IExposedProperty, Variation } from '@/models/property';
import { updateRoomParams } from '@/stores/booking';
import app_store from '@/stores/app.store';
import { generateColorShades, getUserPrefernce, setDefaultLocale } from '@/utils/utils';
import Stack from '@/models/stack';
// import { roomtypes } from '@/data';
@Component({
  tag: 'ir-booking-engine',
  styleUrl: 'ir-booking-engine.css',
  shadow: true,
})
export class IrBookingEngine {
  @Prop({ mutable: true }) token: string =
    'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..ySJjLhWwUDeP4X8LIJcbsjO74y_UgMHwRDpNrCClndc';
  @Prop() propertyId: number;
  @Prop() baseUrl: string;
  @State() selectedLocale: Locale;
  @State() property: IExposedProperty;
  @State() currencies: ICurrency[];
  @State() languages: IExposedLanguages[];
  @State() currentPage: pages = 'booking';
  private commonService = new CommonService();
  private propertyService = new PropertyService();
  @State() router = new Stack<HTMLElement>();
  async componentWillLoad() {
    axios.defaults.baseURL = this.baseUrl;
    getUserPrefernce();
    this.token = await this.commonService.getBEToken();
    // if (this.token !== '') {
    //   this.initializeApp();
    // }
    // this.router.push(<ir-booking-page></ir-booking-page>);
  }
  @Watch('token')
  handleTokenChange(newValue: string, oldValue: string) {
    if (newValue !== oldValue) {
      this.initializeApp();
    }
  }
  initializeApp() {
    this.commonService.setToken(this.token);
    this.propertyService.setToken(this.token);
    app_store.app_data = {
      token: this.token,
      property_id: this.propertyId,
    };
    this.initRequest();
  }
  async initRequest() {
    const [property, currencies, languages] = await Promise.all([
      this.propertyService.getExposedProperty({ id: this.propertyId, language: app_store.userPreferences?.language_id || 'en' }),
      this.commonService.getCurrencies(),
      this.commonService.getExposedLanguages(),
    ]);
    this.property = property;
    this.currencies = currencies;
    this.languages = languages;
    const p = JSON.parse(localStorage.getItem('user_prefernce'));
    if (!p) {
      setDefaultLocale({ currency: this.property.currency });
    }
    // booking_store.roomTypes = [...roomtypes];
    if (this.property.space_theme) {
      const root = document.documentElement;
      const shades = generateColorShades(this.property.space_theme.button_bg_color);
      let shade_number = 900;
      shades.forEach((shade: any, index) => {
        root.style.setProperty(`--brand-${shade_number}`, `${shade.h}, ${shade.s}%, ${shade.l}%`);
        if (index === 9) {
          shade_number = 25;
        } else if (index === 8) {
          shade_number = 50;
        } else {
          shade_number = shade_number - 100;
        }
      });
    }
  }
  handleVariationChange(e: CustomEvent, variations: Variation[], rateplanId: number, roomTypeId: number) {
    e.stopImmediatePropagation();
    e.stopPropagation();
    const value = e.detail;
    const selectedVariation = variations.find(variation => variation.adult_child_offering === value);
    if (!selectedVariation) {
      return;
    }
    console.log(selectedVariation);
    updateRoomParams({ params: { selected_variation: selectedVariation }, ratePlanId: rateplanId, roomTypeId });
  }
  @Listen('routing')
  handleNavigation(e: CustomEvent) {
    e.stopImmediatePropagation();
    e.stopPropagation();
    console.log(e.detail);
    this.currentPage = e.detail;
  }
  render() {
    if (!this.property) {
      return null;
    }
    // const currentPage = this.router.peek();
    return (
      <main class="relative  space-y-5 flex flex-col w-full">
        <section class="w-full z-50 sticky top-0 ">
          <ir-nav
            exposed_property={this.property}
            website={this.property?.space_theme.website}
            logo={this.property?.space_theme?.logo}
            currencies={this.currencies}
            languages={this.languages}
          ></ir-nav>
        </section>
        <section class="flex-1 px-4 lg:px-6">{this.currentPage === 'booking' ? <ir-booking-page></ir-booking-page> : <ir-checkout-page></ir-checkout-page>}</section>

        <ir-footer exposedProperty={this.property}></ir-footer>
        {/* <button onClick={() => this.router.push(<ir-checkout-page></ir-checkout-page>)}>checkout</button> */}
      </main>
    );
  }
}
